import {commumal,listWindow,descroll} from "../public"
var  overList={
     methods:{
         init(){
            $(".weui-btn.query").on("click",overList.events.openWindow);
            $(".condition ul li i").on('click',listWindow.defaultDelete)
         }
     },
     events:{
        openWindow(){
            listWindow.backToStartPoint();
            listWindow.query({
                cache:"DATE",
                loadCondition:(data)=>{
                    overList.events.loadCondition(data)
                },
                fresh:(data)=>{
                    overList.events.fetchData(data,{refresh:true,complete:false})
                }
            })
        },
        loadCondition(data){
            let html="";
            for(let i in data){
               html+=`
                 <li value="${data[i].value.index}">
                     <span>${data[i].name}</span>
                     <i class="iconfont icon-x"></i>
                 </li>
               `
            }
           let $html=$(html);
            $html.find("i").on("click",overList.events.deleteli)
            $(".condition ul").html($html)
         },
         deleteli(){
            listWindow.backToStartPoint()
            let $li=$(this).closest("li");
            let index=$li.attr("value");
            let arr=JSON.parse(commumal.caching("DATE"));
            for(let i=0;i<arr.length;i++){
                if(arr[i].value.index==index){
                    arr.splice(i,1)
                }
            }
            overList.events.fetchData(arr,{refresh:true,complete:false})
            localStorage.setItem("DATE",JSON.stringify(arr))
            $li.remove()
         },
         fetchData(data,ref){
            let submitData={}
            for(let i in data){
                if(data[i].value.index==0){
                   submitData.QUERY=data[i].value.value   //取input框里面的内容
                }else{
                    let time=data[i].value.value;
                    submitData.DATE_FRM=time.start;
                    submitData.DATE_TO=time.end;   //取开始结束时间
                }
            }
            if(ref.refresh){
                descroll.config.page.index=1;
              }
            let date=moment().format('YYYY-MM-DD');
          
            let endData={
                m:"LIST",
                SP_NAME:"SP_OTBL_OVERTIME_BILL_INFO_LIST",
                SELF_IND:"Y",
                QUERY:submitData.QUERY||"",
                DATE_FRM:submitData.DATE_FRM||date,
                DATE_TO:submitData.DATE_TO||date,
                page:descroll.config.page.index++,
                rows:descroll.config.page.rows
            }
            
             console.log("提交的数据:",endData);
             overList.events.fresh(endData,ref)
         },
         fetchCache(ref){ //判断是否有缓存
            let arr=JSON.parse(commumal.caching("DATE"));
            let time=moment().format('YYYY-MM-DD');
            if(!arr){
                let value=[{name:"今天",value:{index:1,value:{start:time,end:time}}}]
                overList.events.fetchData(value,ref);
                $(".condition ul li").attr("value",1)
            }else{
                overList.events.loadCondition(arr);
                overList.events.fetchData(arr,ref)
            }
        },
        fresh(subData,ref){
            $.defs.loadingshow();
            $.defs.myPost(subData).then((res)=>{
                $.defs.loadinghide();
                for(let i=0;i<res.rows.length;i++){
                    res.rows[i].ApplyDate=commumal.dateToday(res.rows[i].OTBL_APPLY_DT)
                }
             
                console.log(res)
                let html=overList.events.content(res.rows);
                let $html=$(html);
                $html.find(".delete").on("click",overList.events.delete);
                $html.on("click",overList.events.matterClick);
                descroll.config.pages(res);
                if(ref.complete){
                    if(res.rows.length!=0){
                         $(".content").append($html);
                          descroll.config.isLoad=true;
                    }else{
                        $(".content").empty()
                    }
                }else{
                    if(res.rows.length!=0){
                        $(".content").html($html)
                    }else{
                        $(".content").empty()
                    }
                }
                 
            })
        },
        content(data){
            var html='';
            for(let i=0;i<data.length;i++){
                html+=`
                <div class="weui-form-preview item" index="${data[i].OTBL_KY}" lvlvId="${data[i].OTBL_ID}"  sts="${data[i].OTBL_STS}">
                  <div class="weui-form-preview__hd">
                    <div class="weui-form-preview__item">
                       <label class="weui-form-preview__label black f12">${data[i].OTBL_ID}</label>
                       <em class="weui-form-preview__value">${data[i].OTBL_STS_DESC}</em>
                     </div>
                 </div>
                 <div class="weui-form-preview__bd f12">
                   <div class="weui-form-preview__item">
                      <label class="weui-form-preview__label black">${data[i].MEME_NAME}</label>
                      <span class="weui-form-preview__value black">加班时长:${data[i].OTBL_HOUR}小时</span>
                   </div>
                  <div class="weui-form-preview__item">
                    <label class="weui-form-preview__label black">加班时间</label>
                    <span class="weui-form-preview__value black f12">${data[i].OTBL_START_DT}至${data[i].OTBL_END_DT}</span>
                  </div>
                 <div class="weui-form-preview__item">
                     <label class="weui-form-preview__label black">申请时间</label>
                     <span class="weui-form-preview__value black f12">${data[i].ApplyDate}</span>
                 </div>
                 </div>
                 <div class="weui-form-preview__ft">
                    ${overList.events.operationBtn(data[i])}
                 </div>
                </div>
                `
            }
            return html
        },
        operationBtn(data){
            let html=''
           if(data.OTBL_STS<431){
               html+=`<a class="weui-form-preview__btn weui-form-preview__btn_primary delete" href="javascript:">删除</a>`  
            }
            return html;
         },
        delete(ev){
            ev.stopPropagation();
            ev.preventDefault();
            var $item=$(this).closest(".weui-form-preview");
            var otblKy=$item.attr("index");
            var otblId=$item.attr("lvlvid");
            commumal.confirm({
                 title:'确定删除单号为'+otblId+"的加班申请",
                 click:()=>{
                     overList.events.deleteItem(otblKy)
                 }
            })
            
        },
        deleteItem(key){
            $.defs.loadingshow();
            $.defs.myPost({
                m:"delete",
                SP_NAME:"SP_OTBL_OVERTIME_BILL_INFO_DELETE",
                REVIEW_IND:"D",
                OTBL_KY:key
            }).then((data)=>{
                $.defs.loadinghide();
                if(data.RETURN_CODE==0){
                    overList.events.fetchCache({refresh:true,complete:false});
                    weui.toast('删除成功', 3000);
                }else{
                    $.defs.msg(data.RETURN_MESSAGE)
                }
            })
        },
        matterClick(){
            var $item=$(this).closest(".weui-form-preview");
            var otblKy=$item.attr("index");
            var sts=$item.attr("sts");
            spspa.setMessage("editID",{
                'content': {
                    otblKy:otblKy
                }
            });
            if(sts<431){
                spspa.go("/add")
            }else{
                spspa.go("/myQuery")
            }
        }
     },
     config:{
         init(){
            $(".top .huan").text("加班申请>我的");
            overList.events.fetchCache({refresh:true,complete:false}) 
         }
     }
}
$(function(){
     overList.config.init();
     overList.methods.init();
})


